body{
    width: 100%;
    position: absolute;
    
}

table {
    position: relative;
    margin:10px auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-collapse: collapse;
    width:80%;
    resize:both;
    overflow: auto;
    
  }
  
  th, td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  td {
    max-width:100%;
    width:auto;
    min-width:100px;
    }
  th{
    align:center;
    height: 100px;
  }
  tr:hover {
    background-color:#f5f5f5;
    cursor: pointer;
    
}
  img{  
      width: 30px;
      height: 30px;
      margin-right: 10px;
      border-radius: 20%;
  }
  .container{
      display: flex;
      align-items: center;
  }